/* floating action buttons */
.fab {
  display: inline-block;
  height: 56px;
  width: 56px;
  margin: 8px;
  padding: 16px;
  text-align: center;
  border-radius: 50%;
  cursor: pointer;
  outline: 0;
  border: 0;
  color: rgba(0, 0, 0, 0.87);
  text-decoration: none;
  font-family: "Roboto", sans-serif;
  font-size: 24px;
  line-height: 24px;
  vertical-align: middle;
  font-weight: 500;
  transition: background-color .2s, box-shadow .2s; }
  .fab:hover, .fab:focus {
    background: rgba(153, 153, 153, 0.2);
    color: rgba(0, 0, 0, 0.87); }
  .fab:active {
    background: rgba(153, 153, 153, 0.4); }
  .fab.fab-colored {
    background: #607d8b;
    color: #FFF; }
    .fab.fab-colored:hover, .fab.fab-colored:focus {
      background: #546e7a;
      color: #FFF; }
    .fab.fab-colored:active {
      background: #455a64; }
  .fab.fab-accent {
    background: #ff9100;
    color: rgba(0, 0, 0, 0.87); }
    .fab.fab-accent:hover, .fab.fab-accent:focus {
      background: #ff7f1f;
      color: rgba(0, 0, 0, 0.87); }
    .fab.fab-accent:active {
      background: #ff6d00; }
  .fab.fab-colored .rippleAnimate, .fab.fab-accent .rippleAnimate {
    background-color: rgba(255, 255, 255, 0.3); }
  .fab.fab-mini {
    height: 40px;
    width: 40px;
    padding: 8px; }

/* tooltips */
.tooltip {
  position: relative; }
  .tooltip:before {
    content: attr(data-tooltip);
    position: absolute;
    box-sizing: border-box;
    max-width: 160px;
    background: rgba(97, 97, 97, 0.9);
    padding: 6px 16px;
    border-radius: 2px;
    color: #FFF;
    font-size: 14px;
    font-weight: normal;
    text-align: center;
    text-transform: none;
    line-height: 20px;
    white-space: normal;
    opacity: 0;
    transition: opacity .2s;
    bottom: calc(100% + 16px);
    left: 50%;
    pointer-events: none;
    transform: translate(-50%, 0); }
  .tooltip.tooltip-expanded:before {
    opacity: 1; }
  .tooltip.tooltip-top-left:before {
    left: 0;
    transform: translate(0%, 0); }
  .tooltip.tooltip-top-right:before {
    left: auto;
    right: 0;
    transform: translate(0%, 0); }
  .tooltip.tooltip-bottom-left:before {
    bottom: auto;
    top: calc(100% + 16px);
    left: 0;
    transform: translate(0%, 0); }
  .tooltip.tooltip-bottom:before {
    bottom: auto;
    top: calc(100% + 16px); }
  .tooltip.tooltip-bottom-right:before {
    bottom: auto;
    top: calc(100% + 16px);
    left: auto;
    right: 0;
    transform: translate(0%, 0); }
  .tooltip.tooltip-left:before {
    transform: translate(0, -50%);
    right: calc(100% + 12px);
    top: 50%;
    bottom: auto;
    left: auto; }
  .tooltip.tooltip-right:before {
    transform: translate(0, -50%);
    left: calc(100% + 12px);
    top: 50%;
    bottom: auto; }

/* tables */
tr.selected {
  background: #f5f5f5; }

tr.selected:hover {
  background: #eeeeee; }

.table-bordered {
  border: 0;
  border-radius: 2px;
  box-shadow: 0 0 0 1px #e0e0e0; }

.table-striped tbody tr:nth-child(even) {
  background: #FAFAFA; }

.table-striped tbody tr:nth-child(even):hover, .table-striped tbody tr.selected:nth-child(even):hover {
  background: #eeeeee; }

.table-striped tbody tr.selected:nth-child(even) {
  background: #f5f5f5; }

